<template>
  <div class="container">
    <div><textarea @mousemove="printTextBySymbol(text)"  v-model="text"></textarea></div>
    <div class="copiBlock" @click="copyToClipBoard()">{{ForIteration}}</div>
    <div>
      <p>- вставьте текст в желтое поле и переместите мышь в любом направлении в пределах поля</p>
      <p>- исправленный вариант появится в зеленом поле</p>
      <p>- кликните в любое меcто зеленого поля и исправленный вариант скопируется в буфер обмена</p>
      <p>- вставьте исправленный вариант (ctrl+V)</p>
      <p>- наслаждайтесь</p>
    </div>
  </div>

</template>

<script>


export default {
  data(){
    return {
      text: '',
      FirstIteration: '',
      TwoIteration: '',
      TreeIteration: '',
      ForIteration: '',
      count: 0,
      myText: 'Текст для копирования'
    }
  },
  methods: {
    copyToClipBoard(){
        navigator.clipboard.writeText(this.ForIteration); 
      this.text = ''     
    },
    printTextBySymbol(t){
      this.FirstIteration = ''
      this.text = this.text.replace(/ /g, '')
      .replace(/cvc/g, 'cyc')
      .replace(/fert/g, 'tert')
      .replace(/terf/g, 'tert')
      .replace(/ferf/g, 'tert')
      .replace(/imld/g, 'imid')
      .replace(/hvd/g, 'hyd')
      .replace(/aIdehyde/g, 'aldehyde')
      .replace(/aidehyde/g, 'aldehyde')
      .replace(/ainin/g, 'amin')
      .replace(/zoio/g, 'zolo')
      .replace(/zoIo/g, 'zolo')
      .replace(/iinidazo/g, 'imidazo')
      .replace(/rneth/g, 'meth')
      .replace(/iniid/g, 'imid')
      .replace(/suif/g, 'sulf')
      .replace(/l^/g, '1,5')
      .replace(/—/g, '-')
      .replace(/\/7/g, 'H')
      .replace(/\/V/g, 'N')
      .replace(/\/v/g, 'N')
      .replace(/fert/g, 'tert')
      .replace(/terf/g, 'tert')
      .replace(/ferf/g, 'tert')
      .replace(/arnide/g, 'amide')
      for (let i = 0; i < t.length; i++) {
        if (t[i]  == " ") {
          this.count = this.count + 1
        }
         else {
          if(((t[i]  == "i") || (t[i]  == "I") || (t[i]  == "1")) && 
          
          ((t[i-1]  == "y") || 
          ((t[i-1]  == "o") && (t[i+1]  == "e")) ||
          ((t[i-1]  == "c") && (t[i-2]  == "y") && (t[i-3]  == "c") && (t[i+1]  == "o")) ||
          ((t[i-1]  == "o") && (t[i+1]  == "-")) ||
          ((t[i-1]  == "o") && (t[i+1]  == "‐")) ||
          ((t[i-1]  == "o") && (t[i+1]  == ")")) ||
          ((t[i-1]  == "o") && (i  == t.length - 1)) ||
          ((t[i-1]  == "f") && (t[i+1]  == "u") && (t[i+2]  == "o") && (t[i+3]  == "r")) ||
          ((t[i-2]  == "c") && (t[i-1]  == "h") && (t[i+1]  == "o") && (t[i+2]  == "r"))
          )) {
            this.FirstIteration = this.FirstIteration + 'l'
          }
          else {
        if ((t[i]  == "l") && (
          ((i == 0) && ((t[i+1] == '-') || (t[i+1] == '‐') || (t[i+1] == ',') || (t[i+1] == 'H'))) ||
          ((t[i-1] == '-') && ((t[i+1] == '-') || (t[i+1] == '‐') || (t[i+1] == ',') || (t[i+1] == 'H') || (t[i+1] == 'R') || (t[i+1] == 'S') || (t[i+1] == 'E') || (t[i+1] == 'Z') || (t[i+1] == 'r') || (t[i+1] == 's'))) ||
          ((t[i-1] == '‐') && ((t[i+1] == '-') || (t[i+1] == '‐') || (t[i+1] == ',') || (t[i+1] == 'H') || (t[i+1] == 'R') || (t[i+1] == 'S') || (t[i+1] == 'E') || (t[i+1] == 'Z') || (t[i+1] == 'r') || (t[i+1] == 's'))) ||
          ((t[i-1] == '(') && ((t[i+1] == '-') || (t[i+1] == '‐') || (t[i+1] == ',') || (t[i+1] == 'H') || (t[i+1] == 'R') || (t[i+1] == 'S') || (t[i+1] == 'E') || (t[i+1] == 'Z') || (t[i+1] == 'r') || (t[i+1] == 's'))) ||
          ((t[i-1] == '[') && ((t[i+1] == '-') || (t[i+1] == '‐') || (t[i+1] == ',') || (t[i+1] == 'H') || (t[i+1] == 'R') || (t[i+1] == 'S') || (t[i+1] == 'E') || (t[i+1] == 'Z') || (t[i+1] == 'r') || (t[i+1] == 's'))) ||
          ((t[i-1] == '{') && ((t[i+1] == '-') || (t[i+1] == '‐') || (t[i+1] == ',') || (t[i+1] == 'H') || (t[i+1] == 'R') || (t[i+1] == 'S') || (t[i+1] == 'E') || (t[i+1] == 'Z') || (t[i+1] == 'r') || (t[i+1] == 's'))) ||
          ((t[i-1] == ',') && ((t[i+1] == '-') || (t[i+1] == '‐') || (t[i+1] == ',') || (t[i+1] == 'H') || (t[i+1] == 'R') || (t[i+1] == 'S') || (t[i+1] == 'E') || (t[i+1] == 'Z') || (t[i+1] == 'r') || (t[i+1] == 's'))) ||
          ((t[i-1] == ' ') && ((t[i+1] == '-') || (t[i+1] == '‐') || (t[i+1] == ',') || (t[i+1] == 'H') || (t[i+1] == 'R') || (t[i+1] == 'S') || (t[i+1] == 'E') || (t[i+1] == 'Z') || (t[i+1] == 'r') || (t[i+1] == 's'))) ||
          (t[i+1]  == "H") ||
          ((t[i-1] == '.') && (t[i+1] == ']'))
        )
        ) {
          this.FirstIteration = this.FirstIteration + '1'
        } else
          if(((t[i]  == "-") || (t[i]  == "‐")) && ((t[i-1] == ")") || (t[i-1] == "]") || (t[i-1] == "}")) &&
          ((t[i-3] != '(') && (t[i-3] != ',')  && (isNaN(t[i-3])) && (isNaN(t[i+1])) && (t[i+1] != "(") && (t[i+2] != "H") && (t[i+2] != "R") && (t[i+2] != "S") && (t[i+2] != "E") && (t[i+2] != "Z") && (t[i+2] != "-") && (t[i+2] != "‐") && (t[i+1] != "{") && (t[i+1] != "[") && (t[i+1] != "R") && (t[i+1] != "S") && (t[i+1] != "N") && (t[i+1] != "O"))){
            this.FirstIteration = this.FirstIteration + ''
          } else {
            if((t[i]  == "|") && (t[i-1]  == "o")){
              this.FirstIteration = this.FirstIteration + '['
            }else{
              if((t[i]  == "l") && (t[i-1]  == "|")){
                  this.FirstIteration = this.FirstIteration + '1'
              }else {
                if((t[i]  == "|") && ((t[i+1]  == "p"))) {
                    this.FirstIteration = this.FirstIteration + ']'
                } else
                if((t[i]  == "J")) {
                    this.FirstIteration = this.FirstIteration + ']'
                } else {
                  this.FirstIteration = this.FirstIteration + t[i]
                }
              }
            }
          }
          }
        }

    }
    this.TwoIteration = this.FirstIteration
      this.TreeIteration = ''
      for (let i = 0; i < this.TwoIteration.length; i++) {
        if((i == this.TwoIteration.length - 1) && (this.TwoIteration[i] == 'e') && (this.TwoIteration[i-1] == 't') && (this.TwoIteration[i-2] == 'a')){
          this.TreeIteration = this.TreeIteration + this.TwoIteration[i]
          this.TreeIteration = this.TreeIteration.replace(/yl/, 'yl ')
        } else {
          this.TreeIteration = this.TreeIteration + this.TwoIteration[i]
        }
      }
      this.ForIteration = ''
      let tree = this.TreeIteration.replace(/acid/g, ' acid')
      .replace(/bromide/g, ' bromide')
      .replace(/chloride/g, ' chloride')
      .replace(/iodide/g, ' iodide')
      .replace(/fluoride/g, ' fluoride')
      .replace(/fhior/g, 'fluor')
      .replace(/aniin/g, 'amin')
      .replace(/amld/g, 'amid')
      .replace(/chior/g, 'chlor')
      .replace(/arnino/g, 'amino')
      .replace(/hydro chloride/g, ' hydrochloride')
      .replace(/hydrochloride/g, ' hydrochloride')
      .replace(/salt/g, ' salt')
      .replace(/trifluoroacetic/g, ' trifluoroacetic')
      .replace(/ester/g, ' ester')
      .replace(/\)1/g, ')-1')
      for(let i = 0; i < tree.length; i++){
        if(tree[i] == 'N') {
          this.ForIteration = this.ForIteration + 'N'
        } else
        if ((tree[i] == tree[i].toUpperCase()) && ((tree[i] != 'H') && (tree[i] != 'R') && (tree[i] != 'S') && (tree[i] != 'E') && (tree[i] != 'Z') && (tree[i] != 'N') || ((tree[i+1] != ')') && (tree[i+1] != ',') && (tree[i+1] != '-') && (tree[i+1] != '‐') && (tree[i+1] != '/')))){
            this.ForIteration = this.ForIteration + tree[i].toLowerCase()
        } else {
          this.ForIteration = this.ForIteration + tree[i]
        }
      }
    },

  }
}
</script>

